import { useQuery } from "@tanstack/react-query";
import React, { useEffect, useState } from "react";
import { useAlert } from "react-alert";
import { Link, useLocation, useNavigate } from "react-router-dom";
import {
  userRedFlagData,
  userRedFlagDeleted,
} from "../../apiServices/home/homeHttpService";
import { showAlert } from "../commonComponent/alertManager";
import { RotatingLines } from "react-loader-spinner";
import moment from "moment";

function UserRedflag() {
  const [search, setSearch] = useState("");
  const [month, setMonth] = useState("");
  const [year, setYear] = useState("");
  const [loader, setLoader] = useState(false);
  const [delId, setDelId] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(10);
  const alert = useAlert();
  const location = useLocation();
  const navigate = useNavigate();
  const queryParams = new URLSearchParams(location.search);

  useEffect(() => {
    const page = queryParams.get("page") || 1;
    if (page >= 1) {
      setCurrentPage(page);
    }
  }, [currentPage, location.search]);

  const { data: results, refetch } = useQuery({
    queryKey: ["userRedFlagList", search, year, month, currentPage],
    queryFn: async () => {
      const formData = {
        search: search,
        year: year,
        month: month,
        page: currentPage,
        pageSize: itemsPerPage,
      };
      return userRedFlagData(formData);
    },

    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results,
  });

  const userRedFlag = results?.ratings || [];
  const totalPages = results?.totalPages || 0;
  const currentPageNumber = results?.page || 1;

  const delFlag = async (id) => {
    setLoader(true);
    const response = await userRedFlagDeleted(id);
    if (!response.error) {
      setLoader(false);
      document.getElementById("popUpClose2").click();
      showAlert(alert, response.message, { timeout: 3000 });
      refetch();
    } else {
      setLoader(false);
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };

  const handlePageChange = (page) => {
    if (page >= 1 && page <= totalPages) {
      setCurrentPage(page);
      queryParams.set("page", page);
      navigate({ search: queryParams.toString() }, { replace: true });
    }
  };

  const renderPageNumbers = () => {
    const pageNumbers = [];
    const startPage = Math.max(1, currentPage - 2);
    const endPage = Math.min(totalPages, currentPage + 2);

    for (let i = startPage; i <= endPage; i++) {
      console.log(currentPage, i);
      pageNumbers.push(
        <button
          key={i}
          className={`btn ${
            currentPage == i ? "bg-custom-secondary" : "btn-light"
          } ms-1`}
          onClick={() => handlePageChange(i)}
        >
          {i}
        </button>
      );
    }

    return pageNumbers;
  };
  return (
    <>
      <div className="row comman_design mx-0 mt-4">
        <div className="col-12">
          <div className="row align-items-center justify-content-between py-md-4 py-3 px-md-3 px-2">
            <div className="col-md-auto mb-md-0 mb-3">
              <div className="headleft">
                <h2>Users' Red Flag Content</h2>
              </div>
            </div>
            <div className="col-md-4 d-flex align-items-center justify-content-end">
              <div className="searchh_box position-relative">
                <input
                  className="form-control"
                  type="search"
                  placeholder="Search"
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                />
                <button>
                  <i className="fas fa-search" />
                </button>
              </div>
              <div className="dropdown filterdropdown">
                <button
                  className="btn btn-secondary"
                  type="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  <i className="fas fa-filter" />
                </button>
                <div className="dropdown-menu">
                  <form className="form-design row mx-0">
                    <div className="col-12 form-group">
                      <label>Year</label>
                      <select
                        className="form-select"
                        onChange={(e) => setYear(e.target.value)}
                        value={year}
                      >
                        <option value={2001}>2001</option>
                        <option value={2002}>2002</option>
                        <option value={2003}>2003</option>
                        <option value={2004}>2004</option>
                        <option value={2005}>2005</option>
                        <option value={2006}>2006</option>
                        <option value={2007}>2007</option>
                        <option value={2008}>2008</option>
                        <option value={2009}>2009</option>
                        <option value={2010}>2010</option>
                      </select>
                    </div>
                    <div className="col-12 form-group">
                      <label>Months</label>
                      <select
                        className="form-select"
                        onChange={(e) => setMonth(e.target.value)}
                        value={month}
                      >
                        <option value="01">Jan</option>
                        <option value="02">Feb</option>
                        <option value="03">Mar</option>
                        <option value="04">Apr</option>
                        <option value="05">May</option>
                        <option value="06">Jun</option>
                        <option value="07">July</option>
                        <option value="08">August</option>
                        <option value="09">Sep</option>
                        <option value="10">Oct</option>
                        <option value="11">Nov</option>
                        <option value="12">Dec</option>
                      </select>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-12">
          <div className="row comman_table pt-md-3 pb-md-4 pb-3 px-md-3 px-2">
            <div className="col-12">
              <div className="table-responsive">
                <table className="table table-hover">
                  <thead>
                    <tr>
                      <th>S.No.</th>
                      <th>User NAME</th>
                      <th>Email</th>
                      <th>Mobile Number</th>
                      <th>Status</th>
                      <th>Registered At</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {userRedFlag?.map((user, index) => (
                      <tr key={user._id}>
                        <td>
                          {(currentPageNumber - 1) * itemsPerPage + index + 1}
                        </td>

                        <td>{user?.userDetails?.name}</td>
                        <td>{user?.userDetails?.email}</td>
                        <td>{user?.userDetails?.mobileNumber}</td>
                        <td>
                          <span
                            className={
                              user?.userDetails?.status
                                ? "status_accepted"
                                : "status_declained"
                            }
                          >
                            {user?.userDetails?.status ? "Active" : "Inactive"}
                          </span>
                        </td>
                        <td>
                          {moment(user.createdAt).format(
                            "DD MMM YYYY, hh:mm A"
                          )}
                        </td>
                        <td>
                          <div className="d-flex">
                            <Link
                              to={`/admin/view-red-flag-management/${
                                user._id
                              }?tab=${
                                queryParams.get("tab") || 1
                              }&page=${currentPage}`}
                              className="Table_btn me-2"
                            >
                              <i className="fa fa-eye" />
                            </Link>
                            <Link
                              to=""
                              className="Table_btn"
                              onClick={() => setDelId(user._id)}
                              data-bs-toggle="modal"
                              data-bs-target="#popUp2"
                            >
                              <i className="fa fa-trash" />
                            </Link>
                          </div>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
              <div className="pagination row mt-4">
                <div className="col">
                  <span>{`Showing ${currentPageNumber} to ${totalPages}  of ${totalPages}`}</span>
                </div>
                <div className="col-auto d-flex">
                  <button
                    className="bg-main status_accepted_bg"
                    disabled={currentPageNumber == 1}
                    onClick={() => handlePageChange(currentPageNumber - 1)}
                  >
                    Prev
                  </button>
                  {renderPageNumbers()}

                  <button
                    className="bg-main status_accepted_bg  ms-1"
                    disabled={currentPageNumber == totalPages}
                    onClick={() => handlePageChange(currentPageNumber + 1)}
                  >
                    Next
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div
        className="modal fade popUp "
        id="popUp2"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabIndex={-1}
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-body border-0 py-4 px-3">
              <div className="popUp_main text-center">
                <img
                  className="main_icon"
                  src="../assets/img/reject_imgg.webp"
                  alt=""
                />
                <h3>Are You sure, you want to delete?</h3>
                <div className="row mx-0 mt-4 mb-2">
                  <div className="col-6">
                    <Link
                      className="bg-main  status_accepted_bg w-100 d-block"
                      data-bs-dismiss="modal"
                      to=""
                      onClick={() => setDelId("")}
                      id="popUpClose2"
                    >
                      No
                    </Link>
                  </div>
                  <div className="col-6">
                    <button
                      className="bg-custom-secondary status_accepted_bg w-100"
                      onClick={() => delFlag(delId)}
                      disabled={loader}
                    >
                      {loader ? (
                        <>
                          <span className="me-2">Wait</span>
                          <RotatingLines
                            strokeColor="white"
                            strokeWidth="5"
                            animationDuration="0.75"
                            width="18"
                            visible={true}
                          />
                        </>
                      ) : (
                        "Yes"
                      )}
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default UserRedflag;
